import VueButton from '@/components/button/VueButton.vue';
import VueInput from '@/components/input/VueInput.vue';
import { computed, defineComponent, inject, ref } from '@vue/composition-api';
export default defineComponent({
  name: 'Upload',
  components: {
    VueInput: VueInput,
    VueButton: VueButton
  },
  props: {},
  setup: function setup(props, ctx) {
    // 
    var toast = inject('toast'); //  

    var isLoading = ref(false); // 

    var percentComplete = ref(0); // 

    var progressTips = computed(function () {
      return percentComplete.value === 100 ? '' : "".concat(percentComplete.value, "%");
    }); // 

    var isProgressComplete = ref(false);

    var warnMsg = function warnMsg(msg) {
      toast && toast(msg); //  

      isLoading.value = false;
    }; // 


    var uploadFile = function uploadFile() {
      // 
      isProgressComplete.value = false; // 

      isLoading.value = true; // 
      // const fileList = (ctx.refs.file1 as Vue).$el.querySelector('input')?.files;

      var VueInputVm = ctx.refs.file1;
      var fileList = VueInputVm.$refs.input.files; // 

      if (fileList && fileList.length <= 0) {
        return warnMsg('');
      } // 


      var fd = new FormData(); //  FormData 

      fileList && fd.append('avatar', fileList[0]); //  xhr 

      var xhr = new XMLHttpRequest(); // 

      xhr.upload.onprogress = function (e) {
        if (e.lengthComputable) {
          //  
          percentComplete.value = Math.ceil(e.loaded / e.total * 100);
        }
      }; // 


      xhr.upload.onload = function () {
        isProgressComplete.value = true; //  

        isLoading.value = false;
      }; //  open  URL  POST


      xhr.open('POST', 'http://www.liulongbin.top:3006/api/upload/avatar'); //  `onreadystatechange` 

      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4 && xhr.status === 200) {
          var data = JSON.parse(xhr.responseText);
          data.status === 200 ? ctx.refs.img.src = "http://www.liulongbin.top:3006".concat(data.url) : warnMsg(data.message);
        }
      }; // 


      xhr.send(fd);
    };

    return {
      uploadFile: uploadFile,
      percentComplete: percentComplete,
      progressTips: progressTips,
      isProgressComplete: isProgressComplete,
      isLoading: isLoading
    };
  }
});