import "core-js/modules/web.dom-collections.for-each.js";
import "core-js/modules/es.number.constructor.js";
import { delBook } from '@/components/ajax-demo/http-request/bookInfoOperations';
import { getBookInfo } from '@/components/ajax-demo/util/getBookInfo';
import { watchRenderBookList } from '@/components/ajax-demo/util/watchRenderBookList';
import VueButton from '@/components/button/VueButton.vue';
import { defineComponent, inject } from '@vue/composition-api';
export default defineComponent({
  name: 'ResetBookList',
  components: {
    VueButton: VueButton
  },
  setup: function setup()
  /*props, ctx*/
  {
    var eventbus = inject('eventbus');
    var toast = inject('toast');
    var rows;

    var resetBookList = function resetBookList() {
      //  rows
      rows = getBookInfo();
      /*   */
      //  

      if (rows.value.length === 3) {
        return toast && toast('');
      } // 4   delbook()


      rows.value.forEach(function (item) {
        var id = item.id; // id 

        Number(id) > 3 && delBook({
          id: "".concat(id)
        }).then(function (res) {
          if (res.status === 200) {
            var data = res.data.data;
            rows = data;
          }
        }).catch(function (err) {
          return toast && toast("\u5220\u9664\u56FE\u4E66\u5931\u8D25\uFF01".concat(err.message));
        });
      }); // getBookInfo()

      watchRenderBookList(getBookInfo(), eventbus);
      return toast && toast('') && resetBookList(); // 
    };

    return {
      resetBookList: resetBookList
    };
  }
});